import { NgModule } from '@angular/core';
import { ConfigPagesRoutingModule } from './config-pages-routing.module';

import { p500Component } from './erros/500.component';
import { p404Component } from './erros/404.component';

@NgModule({
  imports: [ ConfigPagesRoutingModule ],
  declarations: [
    p404Component,
    p500Component,
  ]
})
export class ConfigPagesModule { }



